import React from 'react';
import { Link } from 'react-router-dom';
import { Navbar, NavDropdown, Button } from 'react-bootstrap';
import { bajaj_capital_logo } from '../../assets/images';
import { withRouter } from "react-router";

class Header extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            isTop: true,
            isOpen: false
        }
    }
    componentDidMount() {
        window.addEventListener('scroll', this.handleScroll);
    }

    componentWillUnmount() {
        window.removeEventListener('scroll', this.handleScroll, false);
    }

    handleScroll = () => {
        const isTop = window.scrollY < 100;
        if (isTop !== this.state.isTop) {
            this.setState({ isTop })
        }
    }

    openNavbar = () => {
        this.setState({ isOpen: !this.state.isOpen });
    }

    redirect = (e, url) => {
        e.preventDefault();
        this.setState({ isOpen: false })
        this.props.history.push(url);
    }



    render() {
        const { isTop, isOpen } = this.state;
        return (
            <React.Fragment>
                <header>
                    <Navbar expanded={isOpen} expand="lg" onToggle={this.openNavbar}
                        className={`navbar navbar-expand-md  navbar-light fixed-top ${!isTop && 'affix'}`} id="header">
                        <div className="container">
                            <Link className="navbar-brand" to="/"><img src={bajaj_capital_logo} alt="bajaj" /></Link>
                            <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                            <Navbar.Collapse id="responsive-navbar-nav">
                                <ul className="navbar-nav  ml-md-auto d-md-flex">
                                    <li className="nav-item ">
                                        <Button variant="link" className="nav-link" onClick={(e) => this.redirect(e, '/')}> Home </Button>
                                    </li>
                                    <li className="nav-item dropdown">
                                        <NavDropdown title="Investments" id="basic-nav-dropdown">
                                            <NavDropdown.Item href="#action/3.1">Mutual Fund</NavDropdown.Item>
                                            <NavDropdown.Item href="#action/3.2">Fixed Deposit</NavDropdown.Item>
                                            <NavDropdown.Item href="#action/3.3">Portfolio Management System</NavDropdown.Item>
                                        </NavDropdown>
                                    </li>
                                    <li className="nav-item">
                                        <Button variant="link" className="nav-link" onClick={(e) => this.redirect(e, '/blogs')}> Insurance </Button>
                                    </li>
                                    <li className="nav-item dropdown">
                                        <NavDropdown title="Loans" id="basic-nav-dropdown">
                                            <NavDropdown.Item href="#action/3.1">Personal Loan</NavDropdown.Item>
                                            <NavDropdown.Item href="#action/3.2">Home Loan</NavDropdown.Item>
                                        </NavDropdown>
                                    </li>
                                    <li className="nav-item">
                                        <Button variant="link" className="nav-link" onClick={(e) => this.redirect(e, '/blogs')}> Credit Cards </Button>
                                    </li>
                                    <li className="nav-item">
                                        <Button variant="link" className="nav-link" onClick={(e) => this.redirect(e, '/blogs')}> Blogs </Button>
                                    </li>
                                    <li className="nav-item ">
                                        <Button variant="link" className="nav-link" onClick={(e) => this.redirect(e, '/about')}> About </Button>
                                    </li>
                                </ul>
                            </Navbar.Collapse>
                        </div>
                    </Navbar>
                </header>
                {this.props.children}
            </React.Fragment>
        )
    }
};

export default withRouter(Header);