import React from 'react';
import Slider from "react-slick";
import { Fixed_Deposit, PMS, AIF, Home_Loan } from '../../../../assets/images';
export default class SubProducts extends React.Component {
    renderSubProducts = () => {
        const elements = [
            {
                src: Home_Loan,
                title: 'Home Loan',
                description: 'Your Dream Home Awaits',
                link: 'https://onebajaj.capital/loans/campaign/home-loan/002/apply-now.html'
            }, {
                src: Fixed_Deposit,
                title: 'Fixed Deposits',
                description: 'Invest now & get fixed returns',
                link: 'https://onebajaj.capital/fixed-deposit/campaign/001/apply-now.html'
            },
            {
                src: PMS,
                title: 'Portfolio Management Services',
                description: 'Invest in ABM India Consumption Fund',
                link: 'https://onebajaj.capital/portfolio-management-services/campaign-001/apply-now-abm-pms.html'
            },
            {
                src: AIF,
                title: 'Alternative Investment Fund',
                description: 'Invest in Indiabulls AIF with Bajaj Capital',
                link: 'https://onebajaj.capital/alternative-investment-fund/campaign-001/apply-now-indiabulls-aif.html'
            },

        ];
        return elements.map((item) => {
            return (<div className="iconbox col-sm" key={item.title}><a href={item.link} target="_blank" rel="noopener noreferrer"> <div className="img"> <img alt="bajaj" src={item.src} /><h5>{item.title}</h5> {item.description}</div>
            </a></div>)
        })
    }
    render() {
        const settings = {
            dots: false,
            infinite: true,
            speed: 500,
            slidesToShow: 3,
            slidesToScroll: 1,
            autoplay: true,
            arrows: false,
            responsive: [
                {
                    breakpoint: 1080,
                    settings: {
                        slidesToShow: 2,
                        slidesToScroll: 1
                    }
                }, {
                    breakpoint: 800,
                    settings: {
                        slidesToShow: 1,
                        slidesToScroll: 1
                    }
                }
            ]
        };

        return (
            <div className=" container-fluid highlighter">
                <div className="container">
                    <Slider {...settings}>
                        {this.renderSubProducts()}
                    </Slider>
                </div>
            </div>
        );
    }
}