import React, { Suspense, lazy } from 'react';
import Products from './components/products/products';
import SubProducts from './components/subProducts/subProducts';
import Spinner from '../../components/spinner/spinner';
const TrendingProducts = lazy(() => import('./components/trendingProducts/trendingProducts'));
const WhyBajaj = lazy(() => import('./components/whyBajaj/whyBajaj'));
const WhyNumber = lazy(() => import('./components/whyNumber/whyNumber'));
const ClientSayings = lazy(() => import('./components/clientSayings/clientSayings'));
const Partners = lazy(() => import('./components/partners/partners'));
const Blogs = lazy(() => import('./components/blogs/blogs'));
const Awards = lazy(() => import('./components/awards/awards'));
const GetInTouch = lazy(() => import('./components/getInTouch/getInTouch'));
export default class Dashboard extends React.Component {
    constructor(props) {
        super(props);
        this.elements = [
            <TrendingProducts key="TrendingProducts" />,
            <WhyBajaj key="WhyBajaj" />,
            <WhyNumber key="WhyNumber" />,
            <ClientSayings key="ClientSayings"  />,
            <Partners key="Partners"  />,
            <Blogs key="Blogs"  />,
            <Awards key="Awards" />,
            <GetInTouch key="GetInTouch"  />
        ];
        this.state = {
            renderElements: [],
            rendered: 0
        }
    }
    componentDidMount() {
        window.addEventListener('scroll', this.handleScroll);
    }

    componentWillUnmount() {
        window.removeEventListener('scroll', this.handleScroll, false);
    }

    handleScroll = () => {
        this.setState({
                renderElements: [...this.elements] 
            });
        window.removeEventListener('scroll', this.handleScroll, false);
        // if (((window.scrollY + window.innerHeight) > (document.documentElement.scrollHeight - 500)) 
        //     && (this.state.rendered < this.elements.length)) {
        //     const { rendered } = this.state;
        //     this.setState({ 
        //         rendered: rendered + 1,
        //         renderElements: [...this.state.renderElements, this.elements[rendered]] 
        //     });
        // }
    }

    render() {
        const { renderElements } = this.state;
        return (
            <main role="main">
                <Products />
                <SubProducts />
                <Suspense fallback={<Spinner />}>
                    {renderElements}
                </Suspense>
            </main>
        )
    }
}
